.filters {
  padding: 60px 0;
  display: flex;
  place-content: center;
  justify-content: space-between;
}
@media (max-width: 990px) {
  .filters {
    flex-direction: column;
    justify-content: start;
    gap: 10px;
  }
}

.search {
  position: relative;
}
.search input {
  font-size: 18px;
  font-family: "PT Sans", sans-serif !important;
  background-color: rgba(226, 236, 226, 0.781);
  letter-spacing: 0.2rem;
  border: none;
  cursor: pointer;
  border-radius: 10px;
  width: 300px;
  padding: 10px 50px;
  outline: 0;
}
.search span {
  position: absolute;
  top: 15px;
  left: 20px;
}